luid-timespan {
	@extend %lui_input_holder_base;

	> input {
		@extend %lui_input_field_base;
		@extend %lui_input_text_field;
		width: map-gets($vars, timespanpicker, width);
	}
}
// Invalid field
luid-timespan.invalid > input,
luid-timespan.ng-invalid > input {
	@extend %lui_invalid_input_base;
}
