#{$prefix}.search.input {
	@include lui_make_icon("search");

	> input { padding-left: map-gets($vars, search, paddingLeft); }

	&.searching {
		@include lui_loader();
		&:after { margin-left: 0; left: 1em; }
	}

	&:before, &:after {
		cursor: text;
		display: inline-block;
		position: absolute;
		top: 50%; left: 1em;
		-ms-transform: translate(0, -50%);
		transform: translate(0, -50%);
		@extend %lui_user_select_none;
	}

	i.empty {
		@include lui_make_icon("cross");
		font-style: normal;
		cursor: pointer;

		font-size: (0.5 * map-gets($luiTheme, elements, typography, bodyFontSize));
		width: map-gets($luiTheme, elements, typography, bodyFontSize); height: map-gets($luiTheme, elements, typography, bodyFontSize);
		text-align: center;
		border-radius: 50%;

		&, &:before {
			position: absolute;
			top: 50%; transform: translateY(-50%);
		}
		right: 1em;
		&:before { right: 0.5em; }

		background: map-gets($vars, borderColor);
		color: #FFF;
	}
}
